	module.exports = {"data":{"markdownRemark":{"html":"<p>まず始めに、今日は告知のみの更新ですのでご了承を。 さて本題。C72はちょいお休みする方向で考えています。今企画しているものが少々大きいのでどう考えてもC72には間に合わない、今から何か考えたところで中途半端モノしか出来る自信がないと考えたためです。その代わりC73はちょっとした小品を製作し、C74辺りには流石に間に合うかなー？って楽観視してみるテスト。 以上建前。以下本音。やっべ郵便局行く時間すら確保できなかったひゃぁいヽ(ﾟ∀ﾟ*)ﾉ いや建前の方も嘘じゃなくてマジだったりするんですがね。つーか作りたいものがありすぎてホントに来年一杯で終わるかなぁ、な状況。 'Ａ｀ あ、そうだ、体験版C2.02も出すんだったやらなきゃ orz</p>\n<h2>(拍)ごめん、夢終劇買う金無い（ノ￣＿￣）ノ～～凸</h2>\n<p>あるあるｗｗｗｗｗ</p>\n<h2>(拍)ジャンル(東方)の底辺で頑張ってください</h2>\n<p>ｶﾞﾝｶﾞﾙ（｀・ω・´）</p>\n<h2>(拍)ｋｓｇってなにさ？「カスがッ！！」？</h2>\n<p>VIP語くらい普通に使わせてくれよ 'Ａ｀ それとも逝って良し！とかｵﾏｴﾓﾅｰとか言って欲しいのんか？</p>\n<h2>(拍)次回作も東方STGなんですか？</h2>\n<p>東方のSTGなんです。</p>\n<h2>(拍)ていうかさ、本家から苦情とか来ないの？～</h2>\n<p>そう言う件で苦情は来てないですね。騙りくさいの含めたら何件かありましたが。まぁ、次回も東方の名を冠しますがそれでも本家じゃやらないようなタイトルを予定しているんで。</p>\n<h2>(拍)ここまで作りこめるのならオリジナルで勝負していただきたいのですが</h2>\n<p>あれ、これオリジナルなんですｇ（はいはい釣り＆ハッタリ乙） 単にえーき様やブンブン丸と紅魔郷～永夜抄形式での直接スペルカード対決が見たかった。魅魔様との弾幕決闘を今一度体感したかった。こういう弾幕は彼女らしくないか？と考えたりもした。そして、たまたま構想していたストーリーに当てはめた。これだけじゃ理由として足らんかのぅ 'Ａ｀「ボクの考えたスペルカード」を具現化してはいけないのだろうか？それもスペルカードだけじゃ足らずドット絵から音楽からステージからストーリーまで全てを盛り込んだ、それが夢終劇だった、てだけですね。 端的に言ってしまうとまたkomkom.comのKOM氏の言葉を借りるが「東方とか、好きだから──」この一言に尽きます。</p>\n<h2>(拍)～どうしてこんなに重いの？</h2>\n<p>「インタプリタ」でぐぐってみるときっと理解出来るんじゃないかな。なので次回こそ弾幕風から離れようかな、と考えているわけで。スペックスペックスペックスペックスペック不足～</p>\n<h2>(拍)～久しぶりに新鮮な「幻想郷的」弾幕に触れたような気がします。～</h2>\n<p>ありがとうございますー。</p>\n<h2>(拍)～むしろあれでは人生オワタに繋がりそうな気が</h2>\n<p>幻想郷の女の子たちは現の人間よりずっと気がしっかりしてるので大丈夫でしょう。</p>\n<h2>(拍)めがねとは何か</h2>\n<p>最近コンタクト派ー。</p>\n<h2>(拍)～1日何時間寝てるんですか？～</h2>\n<p>最近は年の所為かとんと体調崩しやすくなってきたんで4時間くらいですかね。時封城作ってたときはこの1/3位でもぜんぜん平気だったんですが。あぁ働いちまった以上俺は負け組だよなぁ 'Ａ｀</p>\n<h2>(拍)って言うかそっち方面のキャラで固定しましたか…ナイス！！</h2>\n<p>よし、まず一人釣れた！！</p>\n<h2>(拍)キャラ設定テキストはないんですか？</h2>\n<p>vR1.02以降で本体のどっかに隠れています。何かをすると見られるようになります。(vR1.00から実装していたにはいたんですが、表示しないようになってました)</p>\n<h2>(拍)立絵とストーリーと効果音と重いこと意外はかなりよかったよ</h2>\n<p>予想通りのご感想、ありがとうございますー。</p>\n<h2>(拍)送れてるのか？これ</h2>\n<p>しまった特設ページには「チルノでも分かるweb拍手もどきの使い方」にリンク貼るの忘れてたぜ 'Ａ｀</p>\n<h2>(拍)悲しいながらも強く引き込まれるストーリーだったかと。弾幕は少しぬるいか？～</h2>\n<p>難易度って客観的な指標を示すのが難しいですからねぇ。どうしても作者補正と言う魔物がorz</p>\n<h2>(拍)^w^w^w^</h2>\n<p>＾－＾－＾＾－＾</p>\n<h2>(拍)Eternal Phantasmagoriaが欲しいとです（´・ω・｀）</h2>\n<p>夢終劇CD-ROMの中にMP3がまるまるボンっと入ってまっせー？</p>\n<h2>(拍)パッチファイルを解凍するとthC_ptf.exeが143KBしか再現できずに～</h2>\n<p>もう一度ダウンロードしてみました？</p>\n<h2>(拍)これはよい厨サイトですね</h2>\n<p>不覚っ…！何と言う不覚っ…！！ まだかっ…！！ まだっ…幼まで届かなかったかっ…！！</p>\n<h2>(拍)(F)付いてるのに何でフリーじゃないの？</h2>\n<p>時封城も夢終劇もレイラもFはついてないよー。</p>","frontmatter":{"cover":null,"date":"2007-02-14T00:00:00+09:00","strDate":"2007/2/13","title":"さぁて貰えるのかねぇ"}}},"pathContext":{"path":"/ja/blog/2007/02/14-さぁて貰えるのかねぇ/","langKey":"ja"}}

